import react from 'react';

const HomePage = () => (

  <>
    <h1>Hello everyONe on  my blooog yoohoooooooooo</h1>
    <p>
    C’est peut-être un détail pour vous, mais l’album de Fanelly,
     Metro Stories, ne commence pas par le bref "Prelude" 
     éthéré aux voix presque religieuses, mais avec le "It’s 
     Gonna Make A Little Difference", un titre dédié aux victimes des attentas de 2015.
      Nous avions parlé déjà de ce morceau, à la sensibilité indéniable et sans pour autant que la chanteuse 
      italienne tombe dans la sensiblerie.
    </p>

    <p>
    Mais arrêtons-nous quelques instants sur la piste 2 et ce fameux "Prélude", 
    à la langue mystérieuse : "Og ot tog ev’I / Ni erom yna eb ob tnaw t’nob I tub…" Voilà 
    qui mérite une explication. Suite à une manipulation hasardeuse sur son téléphone 
    (la fonction "reverse"), la musicienne a joué la piste suivante, "One Step Behind"
     – mais à l’envers ("But I don’t want to be any more in / I’ve got to go"). Fanelly 
     commente ainsi sa découverte : "J'ai bien aimé l'esthétique et la langue un peu mystérieuse
      qui se dégageait...une fois en studio j'ai donc
     décidé de le reproduire et 
     de garder cette petite trace  dans l'album en prélude du morceau suivant."
    </p>


  </>


)

export default HomePage;